  --Purple-200: hsl(239, 57%, 85%);
  --Pink-200: hsl(357, 100%, 86%);
  --Grey-800: hsl(212, 24%, 26%);
  --Grey-500: hsl(211, 10%, 45%);
  --Grey-100: hsl(223, 19%, 93%);
  --Grey-50: hsl(228, 33%, 97%);
  --White: hsl(0, 100%, 100%);
}

body {
  font-family: "Rubik", var(--system-ui);
  font-size: 16px;
  background-color: var(--Grey-50);
}

.commentContent,
.sendComment,
.replyComment {
  background-color: var(--White);
  border-radius: 10px;
  padding: 15px 30px;
}

.amountContainer {
  background-color: var(--Grey-100);
  padding: 0px 5px;
  border-radius: 5px;
}

.increBtn,
.decreBtn {
  padding: 5px 10px;
  border: none;
  background-color: var(--Grey-100);
  opacity: 0.5;
  color: var(--Purple-600);
  font-size: 20px;
}

.increBtn:hover,
.decreBtn:hover {
  opacity: 1;
}

.decreBtn {
  margin-left: 2px;
}

.amountText {
  color: var(--Purple-600);
  font-weight: bold;
}

.commentContent ~ .comment {
  padding-left: 15%;
}

.commentContent ~ .comment::before {
  content: "";
  width: 0;
  height: 120px;
  border: 3px solid var(--Grey-100);
  position: absolute;
  margin-left: -90px;
  margin-top: 25px;
}

body,
.commentContent,
.commentHeader,
.actionCommentContainer,
.sendComment,
.replyComment,
.replyActionContainer,
.deleteActionContainer,
.editActionContainer,
.deleteActionDialog,
.deleteCancelAction,
.deleteAcceptAction,
.replyBtn,
.sentBtn,
.replyBtnContainer,
.editCommentContainer {
  display: flex;
}

.editCommentContainer {
  margin-bottom: 15px;
}

.commentEdit {
  margin: 16px 0px;
}

body,
.deleteCancelAction,
.deleteAcceptAction,
.replyBtn,
.sentBtn {
  justify-content: center;
}

.container {
  width: 50%;
}

.commentContent,
.commentHeader,
.actionCommentContainer,
.replyActionContainer,
.deleteActionContainer,
.editActionContainer,
.deleteActionDialog,
.deleteCancelAction,
.deleteAcceptAction,
.replyBtn,
.sentBtn {
  align-items: center;
}

.replyBtnContainer {
  justify-content: flex-end;
}

.commentContent,
.replyComment {
  margin-bottom: 15px;
}

.commentContent {
  gap: 25px;
}

.commentHeader,
.sendComment,
.actionCommentContainer,
.replyComment,
.commentEdit {
  gap: 15px;
}

.replyActionContainer,
.deleteActionContainer,
.editActionContainer {
