}

.replyActionBtn,
.deleteActionBtn,
.editActionBtn {
  border: none;
  background-color: var(--White);
  font-size: 15px;
  font-weight: bold;
}

.replyActionBtn,
.editActionBtn {
  color: var(--Purple-600);
}

.deleteActionBtn {
  color: var(--Pink-400);
}

.profileImg {
  width: 35px;
}

.actionCommentContainer {
  flex: 1;
  justify-content: end;
}

.sendComment,
.replyComment,
.commentEdit {
  align-items: start;
}

.sendComment {
  margin-top: 25px;
}

.commentInput,
.replyInput,
.editCommentInput,
.actionCommentContainer {
