}

.profileName {
  font-weight: bold;
  color: var(--Grey-800);
}

.commentDate,
.commentContentPara,
.delete {
  color: var(--Grey-500);
}

.repliesText {
  color: var(--Purple-600);
  font-weight: bold;
}

.deleteDialog {
  border: none;
  border-radius: 15px;
  padding: 30px;
  width: 325px;
  height: 190px;
}

.deleteActionDialog {
  margin-top: 30px;
}

dialog::backdrop {
