  height: 75px;
  border-radius: 5px;
}

.sendComment {
  padding: 30px 30px;
}

.replyActionBtn:hover,
.editActionBtn:hover,
.deleteActionBtn:hover,
.replyBtn:hover,
.sentBtn:hover,
.updateBtn:hover,
.deleteCancelAction:hover,
.deleteAcceptAction:hover {
  opacity: 0.5;
}

.ownCommentMark {
  background-color: var(--Purple-600);
  color: white;
  font-weight: bold;
  font-size: 13px;
  padding: 1px 10px;
  border-radius: 5px;
}

*:disabled {
  opacity: 0.3;
}

.commentBody {
  height: 75px;
}

.commentContentPara {
  height: 100%;
}

.commentContentText {
  overflow: scroll;
}
